@font-face {
    font-family: SpaceMono;
    src: url('SpaceMono-Regular.ttf');
}

body{
    background-color: #eddbdb;

}

h1{
    font-family: SpaceMono, serif;
}

.navi {
    font-family: SpaceMono, sans-serif;
    font-size:calc(8px + 0.5vw);
    text-decoration: none;
    border: solid;
    border-width: 2px;
    border-radius: 3px;
    margin: .5vh;
    padding:1vh;
    float:right;
    z-index: 50;
    color: #175b69;
    display: flex;
    align-items:center;

}
.navi:visited {
    color: #175b69;
}
.navi:hover {
    background-color: #c4c7c0;
}

.header {
    display: flex;
    width: 100%;
    top: 0;
    border-width: 0;
    border-bottom-width: 3px;
    border-style: solid;
    z-index: 50;
    background: #b7d7e5;
    color: #175b69;
}

#links {
    width:100%;
    left:auto;
    right:0;
    margin-top: 0.5%;
    z-index: 50;

}

#name {
    margin-left: 5px;
    font-family: SpaceMono, sans-serif;
    font-size:calc(12px + 0.6vw);
    padding-left: 2px;
    text-decoration: none;
    width: 50%;
    display: flex;
    align-items:center;

}

#name:visited {
    color: black;
}

* {
    margin-top:0;
    font-family: "Lato", sans-serif;
}

.page {

    position: static;
    width:100%;

}

#home {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    width: 100%;
    font-family: SpaceMono, serif;

}

#profile {
    width:80%;
    grid-column-start: 1;
    grid-column-end: 1;
    grid-row:1;
    display: flex;
    font-family: SpaceMono, serif;
    align-self: center;
}
#more {
    font-size: 20px;
    width:100%;
    grid-column-start: 2;
    grid-column-end: 2;
    display:flex;
    font-family: SpaceMono;
    align-self: center;
}

